import React, { useEffect } from 'react';
import Scrollspy from 'react-scrollspy'

export default function Navbar(props) {
    useEffect(() => {
        var sticky = navbar.offsetTop;
        window.onscroll = function () {
            var navbar = document.getElementById("navbar");


            if (window.pageYOffset >= sticky) {
                navbar.classList.add("sticky")
            } else {
                navbar.classList.remove("sticky");
            }
        }
    }, []);

    const gotoPage = (pageId) => {
        var elmnt = document.getElementById(pageId);
        elmnt.scrollIntoView({ behavior: 'smooth' });
    }
    return (
        <div className={props.stuckTop?"stuck":"cont"}>
            <div className="navbar" id="navbar">
                <div className="inside">
                    <div className="logo" >
                        <a href="/#top"><img src={"https://afaithraf.github.io/my-portfolio/assets/logo.png"} alt="ashraf logo" /></a>
                    </div>
                    <div className="navigation">
                            
  <Scrollspy style={{display: 'flex', padding: 0, margin: 0}} items={ ['aboutP', 'contactP', 'workP'] } currentClassName="current">
    <li><a href="/#aboutP">About me</a></li>
    <li><a href="/#contactP">Contact</a></li>
    <li><a href="/#workP">Projects</a></li>
  </Scrollspy>
                    </div>
                </div>
            </div>
            <style jsx>{`
            .cont{
                   margin-top: 110vh;
                   height: 84px;
            }
            .stuck{
                margin-top: 0;
            }
                .navbar{
                    left: 0;
                    right: 0;
                    background: #111111b3;
                   backdrop-filter: blur(25px);
                    top: 0;
                    color: #f1f1f1;
                    font-weight: 500;
                    z-index: 1;
                    border-top: 1px solid #ffffff38;
                    border-bottom: 1px solid #ffffff38;
                }
                .navbar.sticky{
                    position: fixed;
                }
                .inside{
                    display: flex;
                    flex-direction: row;
                    margin: 0 10vw;
                }
                .logo{
                    margin-right: auto;
                }
                .logo img{
                    margin: 24px 0;
                    height: 30px;
                    transition-duration: 300ms;
                }
                .navbar.sticky .logo img{
                    margin: 14px 0;
                    height: 20px;
                }
                .navigation{
                    display: flex;
                }
                .navigation ul{
                    display: flex;
                    padding: 0;
                    margin: auto;
                }
                .navigation li{
                    list-style: none;
                    padding: 10px 0;
                    margin: 0;
                    width: 120px;
                    text-align: center;
                    display: flex;
                }     
                .navigation li a{
                    text-decoration: none;
                    color: #ffffff;
                    margin: auto;
                }
                .navigation li.current a{
                    color: #ffff00;
                }
                @media (max-width: 840px){
                    .logo{
                        margin-left: auto;
                    }
                    .navigation{
                        display: none;
                    }
                }
            `}</style>
        </div>
    )
}
